<template>
    <div class="content-wrapper">
        <section class="content">
            <div class="container-fluid">
                <div class="container">
                    <div class="def_home">
                        <div class="app-page">
                            <div class="head">
                                <h3>Add Swimming Workouts</h3>
                            </div>
                            <div class="blog">
                                <div class="card card-info">
                                    <div class="card-body">
                                        <form class="form-horizontal">
                                            <div class="card-body">
                                                <div class="input-group mb-3">
                                                    <div class="input-group-prepend quantity_in">
                                                        <b class="input-group-text">Title </b>
                                                    </div>
                                                    <input v-model="name" type="text" id="pages_name"
                                                        class="form-control">
                                                </div>
                                                <div class="form-group">
                                                    <!-- :config="editorConfig" -->
                                                    <ckeditor :editor="editor" @ready="onReady" v-model="title">
                                                    </ckeditor>
                                                </div>
                                            </div>
                                            <div class="item-up">
                                                <div class="title"><span>Videos</span></div>
                                                <div class="timeline-body">
                                                    <div class="yt loading">
                                                        <input placeholder="http://www.youtube.com" v-model="m_video"
                                                            type="text">
                                                    </div>
                                                    <div class="img video" v-for="(urls,i) in video" :key="i">
                                                        <div class="view">
                                                            <img :src="urls.url">
                                                            <div><b>{{urls.title}}</b></div>
                                                        </div>
                                                        <i class="fas fa-times" @click="DeleteRoWyt(i)"
                                                            style="cursor: pointer;">
                                                        </i>
                                                    </div>
                                                </div>
                                                <div class="item-btn">
                                                    <small>Paste the media URL in the input</small>
                                                </div>
                                            </div>
                                            <div class="item-up">
                                                <div class="title"><span>Images</span></div>
                                                <div class="timeline-body">
                                                    <div v-if="DEFIMAGES" class="img DEFIMAGES">
                                                        <img :src="APIURL+'/upload/'+DEFIMAGES">
                                                    </div>
                                                    <div class="img" v-for="(url,i) in image" :key="i">
                                                        <img :src="APIURL+url">
                                                        <i class="fas fa-times" @click="DeleteRow(i)"
                                                            style="cursor: pointer;"></i>
                                                    </div>
                                                    <div v-if="loading" class="img loading">
                                                        <img :src="APIURL+'/upload/'+loading">
                                                    </div>
                                                </div>
                                                <div class="item-btn">
                                                    <label for="userfile" class="btn-i">Upload Image</label>
                                                    <input accept=".jpg,.jpeg,.png" name="userfile"
                                                        @change="uploadImage()" id="userfile" style="display:none"
                                                        type="file">
                                                </div>
                                            </div>
                                        </form>
                                        <div class="item-up seo">
                                            <div class="title"><span>Seo</span></div>
                                            <div class="inp-seo">
                                                <div class="input-group mb-3">
                                                    <div class="input-group-prepend quantity_in">
                                                        <b class="input-group-text">keyword</b>
                                                    </div>
                                                    <input v-model="keyword" type="text" class="form-control">
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div @click="api()" class="item-btn">
                                    <button type="button" class="btn btn-block btn-success btn-lg">Save</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<!-- eslint-disable -->

<script>
    import DecoupledEditor from '@ckeditor/ckeditor5-build-decoupled-document';

    import {
        mapState
    } from 'vuex'
    import {
        mapGetters
    } from 'vuex'
    import {
        mapMutations
    } from 'vuex'
    export default {
        created() {
            if (this.params) {
                this.API(`get.php?action=_workouts&id=${this.params}`, (data) => {
                    if (data) {
                        this.modalContainer(false);
                        this.action(data);
                    }
                });
            }
        },
        data() {
            return {
                video: [],
                m_video: "",
                editor: DecoupledEditor,
                loading: false,
                title: '',
                image: [],
                DEFIMAGES: `defualt.png`,
                keyword: "",
                name: '',
            }
        },
        computed: {

            ...mapState([
                'APIURL',
                'escape'
            ]),
            ...mapGetters([
                'API',
                'UPLOADFILE',
                'getJSon'
            ])
        },
        props: ['params'],
        updated: function () {},
        components: {},
        watch: {
            title: function (str) {

            },
            image: function (value, x) {
                x = (value.length > 0) ? false : `defualt.png`;
                this.DEFIMAGES = x;
            },
            m_video: function (val, regExp) {
                if (val.match(
                        /(?:youtube(?:-nocookie)?\.com\/(?:[^\/\n\s]+\/\S+\/|(?:v|e(?:mbed)?)\/|\S*?[?&]v=)|youtu\.be\/)([a-zA-Z0-9_-]{11})/
                        )) {
                    regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#\&\?]*).*/;
                    regExp = val.match(regExp);
                    if (regExp[7].length == 11) {
                        var url =
                            `http://www.youtube.com/oembed?url=http://www.youtube.com/watch?v=${regExp[7]}&format=json`;
                        this.API(url, (e) => {
                            if (e) {
                                this.modalContainer(false);
                                this.video.push({
                                    title: e.title,
                                    url: e.thumbnail_url,
                                    video: val
                                });
                            }
                        }, true);
                        this.m_video = "";
                    }
                }
            }
        },

        methods: {
            encodeURIArr(arr, types) {
                if (arr.length == 0) return [];
                let arrays = [];
                for (let index = 0; index < arr.length; index++) {
                    if (types == "video") {
                        let obj = {
                            title: encodeURIComponent(arr[index].title),
                            url: encodeURIComponent(arr[index].url),
                            video: encodeURIComponent(arr[index].video)
                        }
                        arrays.push(obj);
                    }
                }
                return arrays;
            },

            action(e = false) {

                if (e) {
                    e = e[0];
                    this.name = e.name;
                    this.title = e.title;
                    this.image = JSON.parse(e.image);
                    this.video = JSON.parse(e.video);
                    this.keyword = e.keyword;
                    return;
                }
                this.title.replace(/</g, '&lt;');
                this.title.replace(/>/g, '&gt;');
                this.title.replace(/\"/g, '');
                this.title.replace(/\\/g, '').replace(/\'/g, '').replace(/\"/g, '');
                let obj = {
                    name: this.name,
                    title: this.title,
                    image: this.image,
                    video: this.encodeURIArr(this.video, "video"),
                    keyword: this.keyword
                };

                return JSON.stringify(obj);

            },

            api(URL) {
                if (this.params) {
                    URL = `update.php?raw=workouts&id=${this.params}&row=id&action=${this.action()}`;
                } else {
                    URL = `set.php?raw=workouts&action=${this.action()}`;
                }
                this.API(URL, (data) => {
                    if (data) {
                        this.modalContainer(false);
                        location.href = "../Workouts"
                    }
                });
            },

            onReady(editor) {
                editor.ui.getEditableElement().parentElement.insertBefore(
                    editor.ui.view.toolbar.element,
                    editor.ui.getEditableElement()
                );
            },

            ...mapMutations([
                'modalContainer',
                'deleteAjax',
            ]),

            uploadImage() {
                this.loading = 'loading.gif';
                this.UPLOADFILE([200, 200], (data) => {
                    if (data.status) {
                        this.image.push(data.url);
                        this.loading = false;
                    }
                });
            },

            DeleteRow(i) {
                this.$delete(this.image, i);
            },

            DeleteRoWyt(i) {
                this.$delete(this.video, i);
            }
        }
    }
</script>
<style>
    .ck-content {
        border: 1px solid #ccc !important;
    }

    .app-page {
        margin: 0 20px;
        background: #f5f5f5;
        padding: 20px;
    }

    .app-page .app-page-content {
        border: 1px solid #ddd;
    }

    .item-up .title span {
        font-size: 15px !important;
        text-align: left
    }

    .item-up .title {
        background: #fff;
        padding: 8px;
        margin: 40px 0 0px 0;
        border: 1px solid #ddd;


    }

    .item-up .timeline-body {
        padding: 20px;
        text-align: center
    }

    .item-up .timeline-body.d {
        padding: 15px;
        background: #f6f6f6
    }

    .item-up .timeline-body img {
        padding: 20px;
        text-align: center
    }

    .item-up span {
        color: black;
        font-family: 'Almarai', sans-serif !important;
        font-weight: 900;
        font-size: 20px;
        margin: 0 10px;
        text-align: center;
        display: block
    }

    .item-up .item-btn {
        padding: 10px;
        background: #f5f5f5;
        border: 1px solid #ddd;
        text-align: left;
    }

    .item-up .btn-i {
        background: #0085ba;
        color: white;
        border: none;
        font-family: 'Almarai', sans-serif !important;
        margin: 0;
        padding: 5px 10px;
    }

    .item-btn {
        margin: 0 auto;
        text-align: center
    }

    .btn-success {
        width: 50%;
        background: #0085ba;
        color: white;
        border: none;
        display: inline-block;
        text-align: center;
        margin: 3px auto;

    }

    .item-up .img img {
        width: 80px;
        height: 80px;
        margin: 5px;
        padding: 5px;
        border: 1.5px solid #ddd;
    }

    .item-up .img {
        display: inline-block;
        border: 2px solid #ddd;
        margin: 5px;
        position: relative;
        padding-top: 15px
    }

    .timeline-body {
        border: 1px solid #ddd;

    }

    .item-up .img i {
        top: 0;
        left: 0px;
        position: absolute;
        background: mediumorchid;
        color: white;
        padding: 3px;
        font-size: 11px;
        display: block;
        width: 100%;
        text-align: left
    }

    .img.video {
        width: 150px;
        padding-bottom: 10px;
        vertical-align: top
    }

    .img.video b {
        color: mediumblue;
        padding: 0 10px;
        font-size: 14px
    }

    .yt input {
        margin-bottom: 30px;

    }

    .img.video img {
        width: 130px;
        height: 130px
    }

    @media screen and (max-width: 600px) {
        .app-page {
            padding: 0;
            margin: 0;
        }
    }
</style>